.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.main-header {
  background-color: #ffffff;
  position: relative;
}
.main-header__logo:hover {
  opacity: 0.8;
}
.main-header__logo:focus {
  opacity: 0.6;
}
.main-header__logo2 {
  margin: auto;
  position: relative;
  cursor: pointer;
  left: -15px;
}
.main-header__logo2:hover {
  opacity: 0.8;
}
.main-header__logo2:focus {
  opacity: 0.6;
}
.main-header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 66px;
  padding: 0 20px;
  position: relative;
}
.main-header__wrapper::before {
  content: "";
  position: absolute;
  width: 34px;
  height: 37px;
  cursor: pointer;
  top: auto;
  bottom: auto;
  left: auto;
}
.main-nav__list {
  margin: 0;
  padding: 0;
}
.site-list {
  text-align: center;
}
.main-nav__logo {
  list-style: none;
  position: relative;
  display: block;
}
.main-nav__logo::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #e6e6e6;
  left: 0;
  bottom: 1px;
}
.site-list__item {
  position: relative;
  list-style-type: none;
}
.site-list__item::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #e6e6e6;
  left: 0;
}
.list-header__text-header-form {
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  display: block;
  padding: 25px 20px 20px;
  color: #000000;
  text-decoration: none;
}
.main-nav__toggle {
  width: 24px;
  height: 16px;
  position: absolute;
  display: block;
  top: 24px;
  right: 20px;
  padding: 0;
  z-index: 2;
  cursor: pointer;
  border: none;
  background-color: #ffffff;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  left: 0;
  top: 0;
  background-color: #000000;
  width: 24px;
  height: 1.5px;
  z-index: 3;
  position: absolute;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 8px 0 0 rgba(0, 0, 0, 0.3), 0 16px 0 0 rgba(0, 0, 0, 0.3);
}
.main-nav--opened .main-nav__wrapper {
  position: absolute;
  background-color: #ffffff;
  min-width: 320px;
}
.main-nav--opened .main-nav__toggle {
  right: 0;
  left: auto;
  top: 25px;
  width: 38px;
  height: 12px;
  padding: 0;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  left: -3px;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover {
  opacity: 0.3;
}
@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--closed .site-list__item .site-list__item--current {
    display: block;
  }
  .main-nav--closed .site-list__item .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--closed .site-list__item .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }
  .main-nav--closed .site-list__item .main-nav--nojs .site-list__item {
    display: block;
  }
  .main-nav--closed .site-list__item .main-nav--nojs .site-list__item--active a {
    padding-right: 21px;
    padding-left: 21px;
    text-align: left;
  }
  .main-nav--closed .site-list__item .main-nav--nojs .user-list__item {
    display: block;
    background-color: #6a4d38;
  }
}
