}
.goods-list::before {
  content: "";
  background-color: #D9D9D9;
  position: absolute;
  width: 280px;
  height: 1px;
  top: 13%;
  left: 0;
}
.goods-list__title {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #222222;
}
.goods-list__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.item__list {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  color: #444444;
  padding-top: 13px;
}
.button-catalog {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #ffffff;
}
.wrapper-goods-list__button {
  padding-top: 115px;
}
.action-catalog {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  padding-top: 54px;
}
.action-catalog__img-background {
  background-image: url("../../img/catalog-backgound.jpg");
  width: 280px;
  height: 290px;
  background-repeat: no-repeat;
  display: block;
}
.action-catalog__img-background::before {
  content: "";
  background-color: #D9D9D9;
  position: absolute;
  width: 280px;
  height: 1px;
  top: 6%;
  left: 20px;
}
.action-catalog__color {
  width: 280px;
  height: 290px;
  background-color: #6ab938;
  opacity: 0.85;
}
.action-catalog__text {
  color: #ffffff;
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  position: absolute;
  bottom: 43px;
  width: 161px;
  display: flex;
  align-items: center;
  text-align: center;
}
.action-catalog__text::before {
  content: "";
  background-image: url("../../img/svg/icon_gift.svg");
  position: absolute;
  height: 78px;
  width: 80px;
  left: 25%;
  bottom: 180%;
}
.iframe {
  border: none;
  display: block;
  width: 320px;
  height: 362px;
}
@media (min-width: 768px) {
  .iframe {
    width: 768px;
  }
}
@media (min-width: 1440px) {
  .iframe {
    width: 1440px;
    padding-top: 75px;
    background-color: #F2F2F2;
  }
  .case__map {
    position: relative;
    z-index: -1;
    height: 100%;
  }
  .case__map::before {
    content: "";
    position: absolute;
    height: 100%;
    z-index: -1;
    background-color: #F2F2F2;
    top: 0;
    min-width: 110px;
    left: calc((50vw - 720px) * -1);
    width: 100vw;
  }
}
